import PressActTipDialog from 'press-ui/press-act-tip-dialog/press-act-tip-dialog.vue';
import PressCell from 'press-ui/press-cell/press-cell.vue';


export default {
  name: 'ActTipDialog',
  i18n: {
    'zh-CN': {
      oneButton: '提示弹窗',
      twoButtons: '确认弹窗',
    },
    'en-US': {
      oneButton: 'Prompt Dialog',
      twoButtons: 'Confirm Dialog',
    },
  },
  components: {
    PressActTipDialog,
    PressCell,
  },
  data() {
    return {
      show: true,
      title: this.t('dialog.title'),
      message: 'Some Tips',
      showCancelButton: true,
      showConfirmButton: true,
      cancelButtonText: this.t('cancel'),
      confirmButtonText: this.t('confirm'),
      dialogTypeList: [
        {
          title: this.t('basicUsage'),
          list: [
            {
              name: 'one',
              title: this.t('oneButton'),
            },
            {
              name: 'two',
              title: this.t('twoButtons'),
            },
          ],
        },
      ],
    };
  },
  methods: {
    cancel(show) {
      this.show = !show;
    },
    confirm(show) {
      this.show = !show;
    },

    onShowDialog(type) {
      if (type === 'two') {
        this.showCancelButton = true;
        this.show = true;
        return;
      }

      if (type === 'one') {
        this.showCancelButton = false;
        this.show = true;
        return;
      }
    },
  },
};
